import http from 'http';
import express from 'express';
import morgan from 'morgan';
import config from './config.js';
import {bitstampToKoinim,koinimToBitstamp} from './common.js';
let app = express();
app.server = http.createServer(app);

// logger
app.use(morgan('dev'));


setInterval(runApp, config.emailCheckInterval);   


export async function smsAttentionNotification() {
        setInterval(runApp, config.smsCheckInterval);   
}

async function runApp() {
 await bitstampToKoinim();
 await koinimToBitstamp();    
}



// bid : satis
